* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    overflow-y: scroll;
}

.App {
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.NumMessages{
  font-size: 25px;
  text-align: center;
}

.TopDisplay{
  display: flex;
  flex-direction: row;
}

.Login {
  display: flex;
  margin: auto;
}

.Compose {
  display: flex;
  width: 100%;
  margin: auto;
}

.Centered {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 10%;
}

