label {
	font-size: calc(12px + 2vmin);
	width: 80%;
  margin: auto;
}

form {
  width: 80%;
  margin: auto;
}

input{
  border-width: 1px;
  font-size: calc(12px + 2vmin);
  width: 80%;
  margin: auto;
  border-color: black; /* Without this, the font size doesn't change */
}

input[type="submit"] {
  height: 50px;
  background-color: teal;
  width: 100%;
  font-weight: bold;

}

h1 {
  font-size: calc(30px + 2vmin);
  text-align: center;
}